<template>
  <div class="pageContent">
    <el-card class="box-card">
      <el-radio-group v-model="shopStyle" class="flex-row-start">
        <div class="flex-row-start mr40">
          <el-radio :label="0">风格一 {{styleId == 0 ? ' （使用中）' : ''}}</el-radio>
          <div class="colorBox mr10 buttonColor1"></div>
          <div class="colorBox fontColor1"></div>
        </div>
        <div class="flex-row-start mr40">
          <el-radio :label="1">风格二 {{styleId == 1 ? ' （使用中）' : ''}}</el-radio>
          <div class="colorBox mr10 buttonColor2"></div>
          <div class="colorBox fontColor2"></div>
        </div>
        <div class="flex-row-start mr40">
          <el-radio :label="2">风格三 {{styleId == 2 ? ' （使用中）' : ''}}</el-radio>
          <div class="colorBox mr10 buttonColor3"></div>
          <div class="colorBox fontColor3"></div>
        </div>
        <div class="flex-row-start mr40">
          <el-radio :label="3">风格四 {{styleId == 3 ? ' （使用中）' : ''}}</el-radio>
          <div class="colorBox mr10 buttonColor4"></div>
          <div class="colorBox fontColor4"></div>
        </div>
        <div class="flex-row-start mr40">
          <el-radio :label="4">风格五 {{styleId == 4 ? ' （使用中）' : ''}}</el-radio>
          <div class="colorBox mr10 buttonColor5"></div>
          <div class="colorBox fontColor5"></div>
        </div>
        <div class="flex-row-start">
          <el-radio :label="5">自定义风格 {{styleId == 5 ? ' （使用中）' : ''}}</el-radio>
          <el-color-picker v-model="colorForm[5].rightColor" class="mr10"></el-color-picker>
          <el-color-picker v-model="colorForm[5].leftColor"></el-color-picker>
        </div>
      </el-radio-group>
    </el-card>
    <el-card class="box-card" style="margin-top:20px;">
      <div class="flex-row-start flex-wrap">
        <div class="box box1 mr40">
          <div class="box1Price" :style="{color:colorForm[shopStyle].leftColor}"><small>￥19.00</small></div>
          <div class="box1Spec" :style="{backgroundColor:colorForm[shopStyle].leftColor}"><small>耦荷色</small></div>
          <div class="box2Spec" :style="{backgroundColor:colorForm[shopStyle].leftColor}"><small>s</small></div>
          <div class="box1Foot flex-row-start">
            <div :style="{backgroundColor:colorForm[shopStyle].rightColor}"><small>加入购物车</small></div>
            <div :style="{backgroundColor:colorForm[shopStyle].leftColor}"><small>立即购买</small></div>
          </div>
        </div>
        <div class="box box2 mr40">
          <div class="box2Price1" :style="{color:colorForm[shopStyle].leftColor}"><small>￥720.69</small></div>
          <div class="box2Price2" :style="{color:colorForm[shopStyle].leftColor}"><small>￥720.69</small></div>
          <div class="box2Btn" :style="{backgroundColor:colorForm[shopStyle].leftColor}"><small>去结算</small></div>
        </div>
        <div class="box box3">
          <div class="price1" :style="{color:colorForm[shopStyle].leftColor}">
            <small>￥108.80</small>
          </div>
          <div class="box3Foot flex-row-between">
            <div class="price" :style="{color:colorForm[shopStyle].leftColor}">
              <small>￥108.80</small>
            </div>
            <div class="btb" :style="{backgroundColor:colorForm[shopStyle].leftColor}"><small>去结算</small></div>
          </div>
        </div>
      </div>
    </el-card>
    <div class="flex-row-center" style="margin-top:20px;">
      <el-button :loading="subLoading" type="primary" @click="sub">保 存</el-button>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findShopStyle , setShopStyle } from '@/api/shop/shopStyle'
export default {
  data(){
    return{
      subLoading:false,
      shopStyle:0,
      styleId:0,
      colorForm:[
        {
          leftColor:'#FF4444',
          rightColor:'#FFC338'
        },
        {
          leftColor:'#FF4444',
          rightColor:'#555555'
        },
        {
          leftColor:'#1C252D',
          rightColor:'#E2C38B'
        },
        {
          leftColor:'#FF5079',
          rightColor:'#555555'
        },
        {
          leftColor:'#FFAA00',
          rightColor:'#1C252D'
        },
        {
          leftColor:'#FF4444',
          rightColor:'#FFC338'
        }
      ]
    }
  },
  created(){
    this.getColor()
  },
  methods:{
    // 获取店铺风格
    getColor(){
      findShopStyle(this.shop.shopId).then(res=>{
        console.log('获取店铺配色',res)
        this.styleId =res.data.styleId
        this.shopStyle = Number(res.data.styleId)
        if(this.styleId == 5){
          this.colorForm[5] = {
            leftColor:res.data.leftColor,
            rightColor:res.data.rightColor
          }
        } 
      })
    },
    // 提交
    sub(){
      this.subLoading = true;
      let obj = {
        shopId:this.shop.shopId,
        styleId:this.shopStyle,
        leftColor:this.colorForm[this.shopStyle].leftColor,
        rightColor:this.colorForm[this.shopStyle].rightColor,
      }
      console.log('请求的数据',obj)
      setShopStyle(obj).then(res=>{
        console.log('设置店铺风格',res)
        if(res.code == 200){
          this.getColor();
          this.$message({
            message: '设置成功',
            type: 'success'
          });
          setTimeout(()=>{
            this.subLoading = false;
          },1000)
        }else{
          this.subLoading = false;
          this.$message.error(res.message);
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .colorBox{
    width: 40px;
    height: 40px;
    border-radius: 2px;
    &.buttonColor1{
      background-color: #FFC338;
    }
    &.fontColor1{
      background-color: #FF4444;
    }
    &.buttonColor2{
      background-color: #555555;
    }
    &.fontColor2{
      background-color: #FF4444;
    }
    &.buttonColor3{
      background-color: #E2C38B;
    }
    &.fontColor3{
      background-color: #1C252D;
    }
    &.buttonColor4{
      background-color: #555555;
    }
    &.fontColor4{
      background-color: #FF5079;
    }
    &.buttonColor5{
      background-color: #1C252D;
    }
    &.fontColor5{
      background-color: #FFAA00;
    }
  }
  .mr10{
    margin-right: 10px;
  }
  .mr40{
    margin-right: 40px;
  }
  .box{
    border: 1px solid #f7f7f7;
    width: 263px;
    height: 467px;
    position: relative;
    &.box1{
      background: url('../../../assets/images/shopStyle/shopStyle1.png') no-repeat;
      background-size: contain;
      .box1Price{
        position: absolute;
        left: 80px;
        bottom: 251px;
        line-height: 16px;
        font-size: 8px;
      }
      .box1Spec{
        width: 42px;
        height: 17px;
        line-height: 14px;
        border-radius: 9px;
        color: #fff;
        position: absolute;
        left: 52px;
        bottom: 167px;
        text-align: center;
        small{
          font-size: 8px;
        }
      }
      .box2Spec{
        width: 22px;
        height: 17px;
        line-height: 13px;
        border-radius: 9px;
        color: #fff;
        position: absolute;
        left: 46px;
        bottom: 82px;
        text-align: center;
        small{
          font-size: 8px;
        }
      }
      .box1Foot{
        width: 100%;
        height: 31px;
        position: absolute;
        left: 0;
        bottom: 0;
        color: #fff;
        div{
          width: 50%;
          line-height: 31px;
          text-align: center;
          font-size: 8px;
        }
      }
    }
    &.box2{
      background: url('../../../assets/images/shopStyle/shopStyle2.png') no-repeat;
      background-size: contain;
      .box2Price1{
        position: absolute;
        left: 104px;
        top: 176px;
        font-size: 8px;
      }
      .box2Price2{
        line-height: 22px;
        position: absolute;
        left: 80px;
        bottom: 43px;
      }
      .box2Btn{
        width: 70px;
        height: 38px;
        text-align: center;
        line-height: 38px;
        color: #fff;
        position: absolute;
        right: 0;
        bottom: 35px;
        font-size: 12px;
      }
    }
    &.box3{
      background: url('../../../assets/images/shopStyle/shopStyle3.png') no-repeat;
      background-size: contain;
      .price1{
        position: absolute;
        right: 19px;
        bottom: 144px;
      }
      .box3Foot{
        box-sizing: border-box;
        padding-left: 10px;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height:38px;
        .btb{
          width: 100px;
          height: 38px;
          line-height: 38px;
          text-align: center;
          color: #fff;
        }
        .price{
          font-size: 12px;
          span{
            font-size: 8px;
          }
        }
      }
    }
  }
</style>