import request from '@/utils/request'
// 查询分组
export function findShopStyle (id) {
  return request({
    url: `/sys/shopStyle/findShopStyle?shopId=${id}`,
    method: 'get',
  })
}
// 设置店铺颜色风格
export function setShopStyle(data) {
  return request({
    url: `/sys/shopStyle/setShopStyle`,
    method: 'post',
    data: data
  })
}